.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border: none;
  border-radius: 6px;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  position: relative;
  outline: none;

  &:focus {
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.2);
  }

  &:active {
    transform: translateY(1px);
  }

  &.disabled,
  &:disabled {
    opacity: 0.5;
    cursor: not-allowed;
    pointer-events: none;
  }
}

// Sizes
.small {
  padding: 6px 12px;
  font-size: 13px;
}

.medium {
  padding: 10px 20px;
  font-size: 14px;
}

.large {
  padding: 14px 28px;
  font-size: 16px;
}

// Variants
.primary {
  background-color: #667eea;
  color: white;

  &:hover:not(:disabled) {
    background-color: #5568d3;
  }
}

.secondary {
  background-color: #f3f4f6;
  color: #333;

  &:hover:not(:disabled) {
    background-color: #e5e7eb;
  }
}

.danger {
  background-color: #ef4444;
  color: white;

  &:hover:not(:disabled) {
    background-color: #dc2626;
  }
}

.success {
  background-color: #10b981;
  color: white;

  &:hover:not(:disabled) {
    background-color: #059669;
  }
}

// Full width
.fullWidth {
  width: 100%;
}

// Loading state
.loading {
  .content {
    opacity: 0.7;
  }
}

.spinner {
  width: 14px;
  height: 14px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.6s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.content {
  display: inline-flex;
  align-items: center;
}
