.loginContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.loginBox {
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 420px;
  padding: 40px;
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.loginHeader {
  text-align: center;
  margin-bottom: 30px;

  h1 {
    font-size: 28px;
    font-weight: 700;
    color: #333;
    margin: 0 0 8px 0;
  }

  p {
    font-size: 14px;
    color: #666;
    margin: 0;
  }
}

.loginForm {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 8px;

  label {
    font-size: 14px;
    font-weight: 600;
    color: #333;
  }
}

.errorMessage {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background-color: #fee;
  border: 1px solid #fcc;
  border-radius: 6px;
  color: #c00;
  font-size: 14px;

  .errorIcon {
    font-size: 18px;
  }
}

.loginFooter {
  margin-top: 24px;
  padding-top: 24px;
  border-top: 1px solid #e0e0e0;

  .testAccounts {
    text-align: center;
    font-size: 13px;
    color: #666;
    margin: 0;
    line-height: 1.6;

    strong {
      color: #333;
    }

    small {
      color: #999;
    }
  }
}

// Responsive
@media (max-width: 480px) {
  .loginBox {
    padding: 30px 20px;
  }

  .loginHeader h1 {
    font-size: 24px;
  }
}
