.tableWrapper {
  width: 100%;
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  font-size: 14px;
}

.thead {
  background-color: #f9fafb;
  border-bottom: 2px solid #e5e7eb;
}

.th {
  padding: 12px 16px;
  text-align: left;
  font-weight: 600;
  color: #374151;
  white-space: nowrap;
}

.tbody {
}

.tr {
  border-bottom: 1px solid #e5e7eb;

  &.striped:nth-child(even) {
    background-color: #f9fafb;
  }

  &.hoverable:hover {
    background-color: #f3f4f6;
  }

  &.clickable {
    cursor: pointer;
  }
}

.td {
  padding: 12px 16px;
  color: #374151;
}

.emptyState {
  padding: 40px 16px;
  text-align: center;
  color: #9ca3af;
}
